.sevices{
    margin-top: 75px;
}
.stripb{
    height: 200px;
    background-color:#d7d8d8;
    display: flex;
    align-items: center;
    font-size: 20px;
    padding: 20px 200px 20px 300px;
    gap: 100px;
}

.stripa{
    height: 200px;
    background-color:white;
    display: flex;
    align-items: center;
    font-size: 20px;
    padding: 20px 300px 20px 200px;
    gap: 100px;
}

@media screen and (max-width:755px) {
    .sevices{
        margin-top: 35px;
    }
    .stripb{
        height: 200px;
        background-color:#d7d8d8;
        display: flex;
        align-items: center;
        font-size: 12px;
        padding: 5px 10px 10px 50px;
        gap: 50px;
    }
    
    .stripa{
        height: 200px;
        background-color:white;
        display: flex;
        align-items: center;
        font-size: 12px;
        padding: 5px 50px 10px 10px;
        gap: 50px;
    }
}