.FAQCards{
    display: grid;
    grid-template-columns: repeat(3,15fr);
    grid-gap: 80px;
    padding: 30px 10px;
    background-color: white;
    align-items: center;
}
@media screen and (max-width:755px) {
    .FAQCards{
        display: grid;
        grid-template-columns: repeat(1,30fr);
        grid-gap: 20px;
        padding: 30px 10px;
        background-color: white;
        align-items: center;
    }
}